<template>
    <div class="order">
        <header>
            <a href="javascript:history.back(-1)">
                <i class="el-icon-arrow-left"></i>
            </a>
            <span>订单列表</span>
        </header>
        <ul>
            <li v-for="o in orders" class="li">
                <img :src="'https://elm.cangdu.org/img/'+o.restaurant_image_url" class="img">
                <div class="liDiv">
                    <div class="liDiv1">
                        <div @click="toOrderDetail(o)">
                            <div>
                                <h4><span>{{o.restaurant_name}}</span><i class="el-icon-arrow-right"></i> </h4>
                                <p>{{o.formatted_created_at}}</p>
                            </div>
                            <p v-if="auth_time > 0">等待支付</p>
                            <p v-if="!(auth_time > 0)">支付超时</p>
                        </div>
                        <div @click="toOrderDetail(o)">
                            <p>{{o.basket.group[0][0].name}}&nbsp;&nbsp;等{{o.total_quantity}}件商品</p>
                            <p>￥{{o.total_amount}}.00</p>
                        </div>
                    </div>
                    <div class="liDiv2">
                        <div>
                            <span @click="isShow1=!isShow1">去支付（还剩{{minute}}分{{second}}秒)</span>
                        </div>
                    </div>
                </div>
            </li>
        </ul>
        <div class="isShow" v-if="isShow1">
            <div class="isShow1">
                <span></span>
                <span></span>
            </div>
            <p>暂不开放支付接口</p>
            <div @click="isShow1 = !isShow1" class="isShow2">确认</div>
        </div>
        <div class="isShow" v-if="isShow2">
            <div class="isShow1">
                <span></span>
                <span></span>
            </div>
            <p>支付超时</p>
            <div @click="isShow2 = !isShow2" class="isShow2">确认</div>
        </div>
        <footer>
            <router-link to="/msite"><div><i class="el-icon-eleme"></i><p>外卖</p></div></router-link>
            <router-link to="/search"><div ><i class="el-icon-search"></i> <p >搜索</p></div></router-link>
            <router-link to=""><div class="f1"><i class="el-icon-tickets"></i><p>订单</p></div></router-link>
            <router-link to="/profile"><div><i class="el-icon-user"></i> <p>我的</p></div></router-link>
        </footer>
    </div>
</template>

<script>
    export default {
        name: "Order",
        data(){
            return{
                user_id:"",
                orders:[],
                auth_time:900,
                minute:15,
                second:"00",
                isShow1:false,
                isShow2:false,
            }
        },
        created(){
            this.user_id = localStorage.getItem("user_id");
            //获取用户订单
            this.axios.get("/bos/v2/users/"+this.user_id+"/orders?limit=10&offset=0").then(res=>{
                console.log(res);
                this.orders = res;
            }).catch(err=>{});
            this.auth_timetimer();
        },
        methods:{
            toOrderDetail(o){
                this.$router.push("/orderDetail");
                localStorage.setItem("order",JSON.stringify(o));
            },
            auth_timetimer(){
                let auth_timetimer = setInterval(() => {
                    this.auth_time -= 1;
                    if (this.auth_time <= 0) {
                        this.isShow2 = true;
                        clearInterval(auth_timetimer);
                    }
                    this.minute = Math.floor(this.auth_time / 60);
                    this.second = this.auth_time % 60;
                    if (this.second < 10){
                        this.second = "0"+this.second;
                    }
                    if (this.minute < 10){
                        this.minute = "0"+this.minute;
                    }
                }, 1000);
            }

        }
    }
</script>

<style scoped>
    .order{
        width: 100%;
        padding-top: 1.95rem;
        overflow: hidden;
    }
    a{
        text-decoration: none;
    }
    header>a{
        color: #fff;
        position: absolute;
        top: 50%;
        left: .4rem;
        transform: translateY(-50%);
    }
    header span{
        font-size: .8rem;
        color: #fff;
        font-weight: 700;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%,-50%);
    }
    ul{
        padding-bottom: 1.95rem;
    }
    .li{
        background-color: #fff;
        display: flex;
        margin-bottom: .5rem;
        padding: .6rem .3rem 0;
    }
    .img{
        width: 2rem;
        height: 2rem;
        margin-right: .4rem;
    }
    .liDiv{
        flex: 5;
    }
    .liDiv1>div{
        border-bottom: .025rem solid #f5f5f5;
        padding-bottom: .3rem;
        display: flex;
        justify-content: space-between;
    }
    .liDiv1 h4{
        display: flex;
        align-items: center;
        justify-content: flex-start;
        font-size: .75rem;
        color: #333;
        line-height: 1rem;
        width: 9rem;
    }
    .liDiv1 h4 span{
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
    }
    .liDiv1 h4 i{
        font-size: 0.4rem;
        color: #ccc;
        margin-right: 0.2rem;
    }
    .liDiv1>div>div>p{
        font-size: .55rem;
        color: #999;
        line-height: .8rem;
    }
    .liDiv1>div>p{
        font-size: .6rem;
        color: #333;
    }
    .liDiv1>div:nth-child(2){
        display: flex;
        justify-content: space-between;
        line-height: 2rem;
        border-bottom: .025rem solid #f5f5f5;
    }
    .liDiv1>div:nth-child(2)>p:nth-child(1){
        font-size: .6rem;
        color: #666;
        width: 10rem;
    }
    .liDiv1>div:nth-child(2)>p:nth-child(2){
        font-size: .6rem;
        color: #f60;
        font-weight: 700;
    }
    .liDiv2{
        text-align: right;
        line-height: 1.6rem;
    }
    .liDiv2>div{
        display: inline-block;
    }
    .liDiv2>div>span{
        font-size: .55rem;
        color: orange;
        padding: .1rem .2rem;
        border-radius: .15rem;
        border-width: 1px;
        border-style: solid;
        border-color: orange;
    }
    .isShow{
        position: absolute;
        top: 50%;
        left: 50%;
        margin-top: -6rem;
        margin-left: -6rem;
        width: 12rem;
        background-color: #fff;
        padding-top: .6rem;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        border: 1px;
        border-radius: .25rem;
    }
    .isShow1{
        width: 3rem;
        height: 3rem;
        border: .15rem solid #f8cb86;
        border-radius: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
    }
    .isShow1>span:nth-child(1){
        width: .12rem;
        height: 1.5rem;
        background-color: #f8cb86;
    }
    .isShow1>span:nth-child(2){
        width: .2rem;
        height: .2rem;
        border: 1px;
        border-radius: 50%;
        margin-top: .2rem;
        background-color: #f8cb86;
    }
    .isShow>p{
        font-size: .7rem;
        color: #333;
        line-height: .9rem;
        text-align: center;
        margin-top: .8rem;
        padding: 0 .4rem;
    }
    .isShow2{
        font-size: .8rem;
        color: #fff;
        font-weight: 700;
        margin-top: .8rem;
        background-color: #4cd964;
        width: 100%;
        text-align: center;
        line-height: 1.8rem;
        border: 1px;
        border-bottom-left-radius: .25rem;
        border-bottom-right-radius: .25rem;
    }
    footer{
        background-color: #fff;
        position: fixed;
        z-index: 100;
        left: 0;
        right: 0;
        bottom: 0;
        width: 100%;
        height: 1.95rem;

        box-shadow: 0 -0.02667rem 0.05333rem rgba(0,0,0,.1);
    }
    footer div{
        width: 25%;
        height: 1.95rem;
        text-align: center;
        align-items: center;
        float: left;
    }

    footer i{
        width: .8rem;
        height: .8rem;
        margin: .3rem 0 .15rem;
        color: #666;
    }
    .f1 i{
        color:#3190e8;
    }
    footer p{
        font-size: .45rem;
        color: #666;
    }
</style>